.article-item{
    width: calc(33.33% - 20px);
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
    border-radius: 2px;
    border-top: 3px solid #F74B4B;
    margin-bottom: 82px;
    position: relative;
    transition: .3s;
    &__top{
        display: flex;
        font-size: 16px;
        line-height: 100%;
        justify-content: space-between;
        padding: 27px 30px 18px;
        background-color: #fff;
    }
    &__type{
        color: #F74B4B;
        transition: .3s;
        border-bottom: 1px solid #F74B4B;
        &:hover{
            border-color: transparent;
        }
    }
    &__date{
        color: #414345;
        opacity: 0.5;
    }
    &__name{
        font-size: 20px;
        line-height: 130%;
        letter-spacing: -0.03em;
        color: #212121;
        padding: 0 30px 20px;
        border-bottom: 1px solid #E8EDF0;
        background-color: #fff;
        transition: .3s;
    }
    &__desc{
        padding: 20px 30px;
        font-size: 16px;
        line-height: 155%;
        color: #000000;
        position: relative;
        z-index: 2;
        background-color: #fff;
        font-weight: 300;
    }
    &__link{
        background: #F1F5F7;
        padding: 20px 30px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-weight: 500;
        font-size: 16px;
        line-height: 120%;
        color: #202121;
        transform-origin: 100% 0;
        opacity: 0;
        visibility: hidden;
        transform: rotateX(-180deg);
        transition: .5s;
        position: absolute;
        top: 100%;
        width: calc(100% - 1px);
        left: 0;
        z-index: 1;
        box-shadow: 0px 0px 50px rgba(88, 101, 107, 0.25);
        .arrow{
            width: 42px;
            height: 42px;
            background-color: #fff;
            border-radius: 50%;
            display: flex;
            align-items: center;
            transition: .3s;
            justify-content: center;
            svg{
                width: 20px;
                height: 20px;
            }
        }
        &:hover{
            .arrow{
                margin-right: 20px;
            }
        }
    }
    &--article{
        border-color: #F7AC3C;
        .article-item__type{
            color: #F7AC3C;
            border-color: #F7AC3C;
            &:hover{
                border-color: transparent;
            }
        }
    }
    &--news{
        border-color: #DC5C25;
        .article-item__type{
            color: #DC5C25;
            border-color: #DC5C25;
            &:hover{
                border-color: transparent;
            }
        }
    }
    &:hover{
        box-shadow: 0px 0px 50px rgba(88, 101, 107, 0.25);
        .article-item__link{
            transform: rotateX(0deg);
            opacity: 1;
            visibility: visible;
            box-shadow: 0px 0px 50px rgba(88, 101, 107, 0.25);
        }
    }
}

body .article-item.article-item--main{
    width: calc(66.66% - 20px);
    border: 0;
    background: #fff;
    padding: 60px 50px 60px 0;
    margin-bottom: 60px;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    position: relative;
    &::before{
        content: '';
        width: 600px;
        right: 100%;
        top: 0;
        position: absolute;
        height: 100%;
        background-color: #fff;
    }
    .article-item__type{
        padding: 0;
        font-size: 20px;
        color: #F74B4B;
        line-height: 1;
        text-transform: uppercase;
    }
    .article-item__name{
        font-weight: 600;
        font-size: 32px;
        line-height: 125%;
        text-transform: uppercase;
        color: #232323;
        margin-bottom: 20px;
    }
    .article-item__desc{
        font-size: 16px;
        line-height: 150%;
        color: #232323;
    }
    .article-item__name,
    .article-item__desc,
    .article-item__top,
    .article-item__type{
        width: 100%;
        padding: 0;
        border: 0;
    }
    .article-item__link{
        transform: rotateX(0deg);
        opacity: 1;
        visibility: visible;
        position: inherit;
        width: auto;
        padding: 0;
        margin-top: 40px;
        box-shadow: none !important;
        background: none !important;
        .arrow{
            box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.15);
            margin-left: 30px;
        }
    }
}